import { Link, ListItem, ListItemText } from "@material-ui/core"
import AppBar from "@material-ui/core/AppBar"
import Drawer from "@material-ui/core/Drawer"
import Hidden from "@material-ui/core/Hidden"
import IconButton from "@material-ui/core/IconButton"
import List from "@material-ui/core/List"
import { makeStyles } from "@material-ui/core/styles"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"
import MenuIcon from "@material-ui/icons/Menu"
import PropTypes from "prop-types"
import React from "react"

interface Props {
  companyName: string
}

const useStyles = makeStyles(theme => ({
  appBar: {
    // color: "#233348",
    // backgroundColor: "#FFF",
  },
  toolbar: {
    flexWrap: "wrap",
  },
  toolbarTitle: {
    flexGrow: 1,
  },
  drawerList: {
    width: 250,
  },
  drawerToggle: {
    padding: 20,
  },
}))

const Header = ({ companyName }: Props) => {
  const classes = useStyles()
  const [open, setOpen] = React.useState(false)

  const handleDrawerOpen = () => {
    setOpen(true)
  }
  const handleDrawerClose = () => {
    setOpen(false)
  }

  //updates here should update the ids in the scroll to components as well
  const navLinks = [
    // { displayText: "Home", link: "#" },
    { displayText: "Physiotherapy", link: "#physiotherapy" },
    { displayText: "Massage therapy", link: "#massage-therapy" },
    { displayText: "Conditions we treat", link: "#conditions-we-treat" },
    { displayText: "Our team", link: "#our-team" },
  ]

  return (
    <div style={{ position: "fixed", top: 0, width: "100%", zIndex: 1 }}>
      <AppBar position="static" elevation={0} className={classes.appBar}>
        <Toolbar className={classes.toolbar}>
          <Hidden smUp>
            <IconButton
              className={classes.drawerToggle}
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawerOpen}
              edge="start"
            >
              <MenuIcon />
            </IconButton>
          </Hidden>
          <Typography
            variant="h6"
            color="inherit"
            className={classes.toolbarTitle}
            style={{ flexGrow: 1 }}
          >
            RECOVR Physiotherapy
          </Typography>

          <Hidden xsDown>
            {navLinks.map(item => (
              <Link
                color="inherit"
                key={item.displayText}
                href={item.link}
                style={{ marginRight: 20 }}
              >
                <Typography variant="button">{item.displayText}</Typography>
              </Link>
            ))}
          </Hidden>
        </Toolbar>
      </AppBar>
      <Drawer
        variant="temporary"
        anchor="left"
        open={open}
        onEscapeKeyDown={handleDrawerClose}
        onBackdropClick={handleDrawerClose}
        ModalProps={{
          keepMounted: true, // Better open performance on mobile.
        }}
      >
        <List className={classes.drawerList}>
          {navLinks.map(item => (
            <ListItem button key={item.displayText}>
              <Link
                color="inherit"
                key={item.displayText}
                href={item.link}
                style={{ marginRight: 20 }}
                onClick={() => setOpen(false)}
              >
                <ListItemText primary={item.displayText} />
              </Link>
            </ListItem>
          ))}
        </List>
      </Drawer>
    </div>
  )
}

Header.propTypes = {
  companyName: PropTypes.string,
}

Header.defaultProps = {
  companyName: `vHealth`,
}

export default Header
